      <v-img
        :gradient="`to bottom, ${barColor}`"
        v-bind="props"
      />
    </template>

    <v-divider class="mb-2"/>
    <v-list-item>
      <v-list-item-title class="text-uppercase font-weight-regular display-2 d-flex align-center">
