      expand
      nav
    >
      <div/>
      <template v-for="(item, i) in computedItems">
        <template v-if="item.accessRight <= accessLevel">
          <base-item-group
            v-if="item.children"
            :key="`group-${i}`"
            :item="item"
          />
          <base-item
            v-else
            :key="`item-${i}`"
            :item="item"
          />
        </template>
      </template>
      <div/>
    </v-list>
    <v-list
      v-else
      expand
      nav
    >
      <v-list-item>
        <v-list-item-content>Your Access is denied!</v-list-item-content>
      </v-list-item>
    </v-list>
    <v-list-item>
      <v-list-item-content>Version:{{ Version }}</v-list-item-content>
    </v-list-item>
  </v-navigation-drawer>
</template>

