  import { mapState } from 'vuex'
  import { version } from '../../../../../package.json'

  console.log(version)
  export default {
    name: 'DashboardCoreDrawer',

    props: {
      expandOnHover: {
        type: Boolean,
        default: false,
      },
    },

    data: () => ({
      items: [
        {
          icon: 'mdi-view-dashboard',
          title: 'dashboard',
          accessRight: 0,
          to: '/',
        },
