              icon: 'mdi-food-fork-drink',
              to: 'dishes-manage',
            },
            {
              title: '菜品统计',
              icon: 'mdi-calculator-variant',
              to: 'dish-statistics',
            },
            {
              title: '菜品配料',
              icon: 'mdi-calculator-variant',
              to: 'attribute-group',
            },
          ],
        },
        {
          group: '/Tables',
          icon: 'mdi-desktop-mac',
          title: '桌台设置',
          accessRight: 1,
          children: [
            {
              title: '分区设置',
              icon: 'mdi-desktop-tower-monitor',
              to: 'section-manage',
            },
            {
              title: '桌子管理',
              icon: 'mdi-table-chair',
              to: 'tables-manage',
            },
          ],
