          icon: 'mdi-account',
          title: '跑堂',
          accessRight: 1,
          children: [
            {
              title: '跑堂管理',
              icon: 'mdi-account-switch',
              to: 'person-manage',
            },
          ],
        },
        {
          group: '/Bills',
          icon: 'mdi-account-cash',
          title: '订单记录',
          accessRight: 1,
          children: [
            {
              title: '全部账单',
              icon: 'mdi-currency-usd',
              to: 'history-bills',
            },
          ],
        },
        {
          group: '/Printer',
          icon: 'mdi-printer',
          title: '打印机和打印记录',
