              title: '打印单列表',
              icon: 'mdi-clipboard-list',
              to: 'printerlist',
            },
          ],
        },
        {
          group: '/Setting',
          icon: 'mdi-account-cog',
          title: '设置',
          accessRight: 1,
          children: [
            {
              title: 'TaxSetting',
              icon: 'mdi-cash-usd',
              to: 'tax-setting',
            },
            {
              title: '支付设置',
              icon: 'mdi-help',
              to: 'paymentpage',
            },
            {
              title: '过敏源设置',
              icon: 'mdi-help',
              to: 'allergen-setting',
            },
            {
              title: '餐馆身份',
              icon: 'mdi-silver-ware',
              to: 'restaurant-info',
            },
            {
              title: '图片设置',
              icon: 'mdi-silver-ware',
              to: 'restaurant-image',
            },
          ],
        },
      ],
      Version: version + 'Only Order',
    }),

    computed: {
      ...mapState(['barColor', 'barImage', 'accessLevel']),
      drawer: {
        get () {
          return this.$store.state.drawer
        },
        set (val) {
          console.log(val)
          this.$store.commit('SET_DRAWER', val)
        },
      },
      computedItems () {
        return this.items.map(this.mapItem)
      },
    },

    watch: {
      '$vuetify.breakpoint.smAndDown' (val) {
        this.$emit('update:expandOnHover', !val)
      },
    },

    methods: {
      mapItem (item) {
        return {
          ...item,
          children: item.children ? item.children.map(this.mapItem) : undefined,
          title: this.$t(item.title),
        }
      },
    },
  }
